<template>
  <v-container fluid class="fill-height">
    <v-row
      align="center"
      justify="center"
    >
      <v-col
        cols="12"
        sm="10"
        md="8"
        lg="6"
        xl="4"
      >
        <v-card class="elevation-12">
          <v-form
            @submit.prevent="signIn"
            ref="form"
            v-model="valid"
          >
            <v-toolbar class="white--text" color="grey darken-3">
              <v-toolbar-title>Sign in</v-toolbar-title>
            </v-toolbar>
            <v-card-text>
              <v-text-field
                v-model="username"
                prepend-icon="mdi-account"
                label="Username*"
                :rules="[rules.required]"
                required
              ></v-text-field>
              <v-text-field
                v-model="password"
                prepend-icon="mdi-lock"
                label="Password*"
                type="password"
                :rules="[rules.required]"
                required
              ></v-text-field>
              <v-alert
                v-if="authStatus === 'error'"
                dense
                outlined
                type="error"
              >
                <strong>{{ authError }}</strong>
              </v-alert>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                type="submit"
                class="white--text"
                color="grey darken-3"
              >Sing in</v-btn>
            </v-card-actions>
            <v-divider></v-divider>
          </v-form>
          <p class="text-center font-weight-light">Don't have an account yet? <router-link class="font-weight-bold" to="/sign_up">Sign up</router-link></p>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      username: null,
      password: null,
      valid: false,
      rules: {
        required: v => !!v || 'Required field'
      }
    }
  },
  methods: {
    signIn () {
      if (this.$refs.form.validate()) {
        const { username, password } = this
        this.$store.dispatch('signIn', { username, password }).then(answer => {
          this.$store.dispatch('getUserCredentials')
          this.$router.push('/')
        }).catch(err => {
          console.log(err)
        })
      }
    }
  },
  computed: {
    authStatus () {
      return this.$store.getters.authStatus
    },
    authError () {
      return this.$store.getters.authError
    }
  },
  beforeRouteLeave (to, from, next) {
    this.$store.state.auth.authStatus = 'none'
    next()
  }
}
</script>
